// color
$main-color:#82f4d3;
$par:#61e9c2;
$white-color:#e9e9e9;
$black:#000;
$gray:#8f8f8f;
$p:#888;
$white:#fff;
$background:#eeeded;
$border:#cfcfcf61;
$bar:#333;
// attr
p,
h1,
ul {
    margin: 0;
}

a:link,
a:focus,
button,
button:focus,
input,
input:focus {
    text-decoration: none;
    border: none;
    outline: none;
}

// class
// size
%size {
    width: 100%;
    height: 100%;
}

// dimen
%dimen {
    top: 0;
    left: 0;
}

%flex {
    display: flex;
    align-items: center;
}

// %trans
%trans {
    transition: all .7s ease-in-out;
}

a.link {
    height: 40px;
    text-align: center;
    line-height: 40px;
    display: grid;
    background-color: $main-color;
    color: $black;
    font-family: "Poppins-Regular";
    font-size: 15px;
    position: relative;
    @extend %trans;
    &:hover:before {
        position: absolute;
        width: 100%;
        height: 100%;
        content: '';
        border: 2px solid $main-color;
        z-index: 1;
    }
}

// logo
.logo {
    @extend %flex;
    .fa {
        color: $par;
        font-size: 20px;
        @include Xs {
            color: $white;
        }
    }
    p {
        font-size: 25px;
        color: $par;
        font-family: "Ubuntu-Medium";
        @include Xs {
            color: $white;
        }
        span {
            color: $black;
            font-size: 25px;
            font-family: "Ubuntu-Medium";
        }
    }
}

// logo
// links
.link {
    span {
        &::after {
            position: relative;
            @extend %dimen;
            content: "\f067";
            font-family: "Font Awesome 5 free";
            font-weight: 900;
            color: $p;
            @extend %trans;
        }
        &.open::after {
            content: "\f068";
            font-family: "Font Awesome 5 free";
            font-weight: 900;
            color: $p;
        }
    }
}

