// header
header {
    background-image: url(../images/Layer-32.png);
    background-attachment: fixed;
    background-position-y: bottom;
    background-position-x: 50%;
    height: 120vh;
    position: relative;
    .overlay {
        position: absolute;
        @extend %dimen;
        @extend %size;
        height: calc(100% - 20vh);
        background-color: $white-color;
        @include mob580 {
            height: 100%;
        }
        &:after {
            content: '';
            position: absolute;
            @extend %size;
            @extend %dimen;
            clip-path: polygon(47% 0, 100% 0, 100% 100%, 76% 100%);
            background-color: $main-color;
            @include Xs {
                clip-path: polygon(0 0, 100% 0, 100% 76%, 0 47%);
            }
        }
    }
    .content {
        position: relative;
        z-index: 999999;
        @extend %size;
        img {
            width: 480px;
            position: absolute;
            left: 50%;
            top: 65%;
            z-index: 999;
            transform: translate(-50%, -65%);
            @include Lg {
                width: 400px;
            }
            @include Sm {
                left: 69%;
                width: 350px;
            }
            @include Xs {
                display: none;
            }
        }
        // navbar
        .nav-bar {
            position: relative;
            padding: 20px 0px;
            // slide-bar
            .slide-bar {
                position: fixed;
                @extend %size;
                background: $bar;
                right: 0;
                top: 0;
                z-index: 999999;
                transform: scaleX(0);
                transform-origin: right;
                @extend %flex;
                justify-content: center;
                @extend %trans;
                &.open {
                    transform: scaleX(1);
                }
                li {
                    margin: 30px 30px 0 0;
                    a {
                        display: block;
                        text-align: center;
                        color: $white;
                        font-size: 20px;
                        @extend %trans;
                        &:hover {
                            opacity: .632;
                        }
                    }
                }
            }
        }
        // bar
        .search {
            position: fixed;
            z-index: 999999;
            width: 100%;
            left: 50%;
            top: 50px;
            width: 90%;
            transform: translateX(-50%);
            @extend %flex;
            justify-content: space-between;
            .close-search,
            .open-search {
                width: 40px;
                height: 40px;
                line-height: 43px;
                text-align: center;
                color: $white;
                background-color: $main-color;
                border-radius: 50%;
                cursor: pointer;
                @include Xs {
                    width: 30px;
                    height: 30px;
                    line-height: 30px;
                }
            }
            .fa {
                font-size: 20px;
                @include Xs {
                    font-size: 14px;
                }
            }
        }
        // // search
        .search-bar {
            position: relative;
            form {
                position: fixed;
                @extend %size;
                @extend %dimen;
                @extend %trans;
                @extend %flex;
                justify-content: center;
                background-color: rgba(61, 61, 61, 0.98);
                z-index: 99999999;
                opacity: 0;
                visibility: hidden;
                &.open {
                    opacity: 1;
                    visibility: visible;
                }
                // close-search
                .closed {
                    position: relative;
                    width: 400px;
                    transform: scaleX(0);
                    @extend %trans;
                    &.open {
                        transform: scaleX(1);
                    }
                    @include mob580 {
                        width: 300px;
                    }
                    input {
                        width: 100%;
                        height: 50px;
                        padding-left: 20px;
                        border: 1px solid $white-color;
                        border-radius: 100px;
                        background-color: $white-color;
                        @include mob375 {
                            height: 40px;
                        }
                        &::placeholder {
                            font-size: 15px;
                            font-family: "Poppins-Regular";
                            color: $p;
                        }
                    }
                    button {
                        position: absolute;
                        right: 10px;
                        top: 50%;
                        transform: translateY(-50%);
                        width: 30px;
                        height: 30px;
                        line-height: 12px;
                        background: $main-color;
                        border-radius: 50%;
                        color: $white;
                        font-size: 12px;
                    }
                }
            }
            // close-search
            // close-button
            .close-button {
                position: absolute;
                top: 30%;
                transform: translate(-50%, -30%);
                margin-bottom: 100px;
                width: 30px;
                height: 30px;
                background: $main-color;
                color: $white;
                font-size: 20px;
                line-height: 30px !important;
                text-align: center;
                border-radius: 50%;
                display: block;
                font-size: 14px;
            }
        }
        // close-button
        // 
        // toggle
        .toggle {
            position: absolute;
            right: 0;
            top: 50%;
            transform: translateY(-50%);
            z-index: 99999;
            width: 40px;
            height: 40px;
            line-height: 40px;
            text-align: center;
            border-radius: 50%;
            background-color: $black;
            cursor: pointer;
            .fa {
                color: $white-color;
                font-size: 16px;
            }
        }
        // toggle
        // navbar
        // title
        .title {
            width: 40%;
            @include Sm {
                width: 100%;
                margin-top: 100px;
            }
            @include mob580 {
                margin: 0;
            }
            h1 {
                color: $black;
                font-family: "Roboto-Bold";
                font-size: 100px;
                text-transform: capitalize;
                @include Xs {
                    font-size: 50px;
                }
                @include mob580 {
                    text-align: center;
                }
            }
            img {
                display: none;
                @include Xs {
                    display: block;
                    width: auto;
                    max-width: 100%;
                    height: 300px;
                    left: 72%;
                    margin: auto;
                    @include mob580 {
                        top: 50%;
                        left: 50%;
                        transform: translate(-50%, -50%);
                        width: 280px;
                    }
                }
            }
            a {
                width: 240px;
                position: relative;
                left: 70px;
                @include Lg {
                    width: 200px;
                    left: 100px;
                    top: 100px;
                }
                @include Xs {
                    top: 120PX;
                    left: 40px;
                    width: 150px;
                }
                @include mob580 {
                    top: 420px;
                    left: 0;
                    margin: auto;
                }
                &:before {
                    left: -25px;
                    bottom: -10px;
                    @include Xs {
                        left: -10px;
                    }
                }
            }
        }
        // title
        //  .body-content
        .body-content {
            position: relative;
            .text {
                position: absolute;
                width: 305px;
                transform: rotate(-90deg);
                top: 150px;
                left: -80px;
                @include Lg {
                    top: -35px;
                    left: 40px;
                    transform: rotate(0deg);
                }
                @include Sm {
                    transform: rotate(0deg);
                    top: -20px;
                }
                @include mob580 {
                    top: 300px;
                    left: 50%;
                    transform: translateX(-50%);
                }
                p {
                    font-family: "Poppins-Regular";
                    color: #8f8f8f;
                    font-size: 14px;
                    @include mob580 {
                        text-align: center;
                    }
                }
            }
            .social {
                position: absolute;
                right: 30px;
                top: 100px;
                z-index: 99;
                @include Lg {
                    top: 70px;
                }
                @include Sm {
                    top: -22px;
                }
                @include Xs {
                    top: 80PX;
                }
                @include mob580 {
                    display: none;
                }
                li {
                    margin-bottom: 30px;
                    @include Lg {
                        margin-bottom: 15px;
                    }
                    a {
                        display: block;
                        text-align: center;
                        i {
                            font-size: 15px;
                            color: $white-color;
                            background-color: $black;
                            width: 40px;
                            height: 40px;
                            line-height: 40px;
                            border-radius: 50%;
                            @extend %trans;
                            @include Xs {
                                width: 30px;
                                height: 30px;
                                line-height: 30px;
                                font-size: 12px;
                            }
                            &:hover {
                                color: $main-color;
                                background-color: $white-color;
                            }
                        }
                    }
                }
            }
        }
    }
}

// header
// .about-us
.about-us {
    padding: 100px 0px;
    @include Sm {
        padding: 50px 0px;
    }
    @include Xs {
        padding: 30px 0px;
    }
    position: relative;
    overflow: hidden;
    .content {
        @extend %flex;
        justify-content: space-between;
        .images,
        .text {
            width: 50%;
        }
        .images {
            height: 400px;
            background-color: $background;
            border: 1px solid $border;
            @extend %trans;
            @include Sm {
                display: none;
            }
            .img {
                height: 100%;
                width: 100%;
                @extend %flex;
                justify-content: center;
                border: 8px solid #fff;
                box-shadow: -10px -10px 10px #ddd, 10px 10px 10px #ddd;
                img {
                    width: 70%;
                }
            }
            &:hover {
                transform: scale(1.1);
            }
        }
        .text {
            position: absolute;
            right: -1%;
            top: 60%;
            transform: translate(-12%, -60%);
            overflow: hidden;
            @include Sm {
                position: relative;
                transform: translate(0);
                width: 100%;
            }
            .images-one {
                width: 50%;
                margin: auto;
                height: 380px;
                background-color: $background;
                @extend %flex;
                justify-content: center;
                border: 1px solid $border;
                @extend %trans;
                display: none;
                @include Sm {
                    display: block;
                }
                @include mob580 {
                    width: 80%;
                    height: 350px;
                }
                @include mob375 {
                    height: 300px;
                }
                .img {
                    width: 100%;
                    @extend %flex;
                    justify-content: center;
                    height: 100%;
                    box-shadow: -10px -10px 10px #ddd, 10px 10px 10px #ddd;
                    border: 8px solid #fff;
                    img {
                        width: 70%;
                    }
                }
                &:hover {
                    transform: scale(1.1);
                }
            }
            h2 {
                font-size: 45px;
                font-family: "CormorantGaramond-Medium";
                color: $black;
                @include Sm {
                    margin: 40px 0px;
                    text-align: center;
                    @include Xs {
                        font-size: 30px;
                    }
                    @include mob375 {
                        font-size: 25px;
                        margin: 20px 0px;
                    }
                }
            }
            p {
                font-size: 15px;
                color: $p;
                font-family: "Poppins-Light";
                line-height: 1.7;
                margin-bottom: 50px;
                @include Sm {
                    width: 80%;
                    margin: 40px auto;
                    text-align: center;
                }
                @include Xs {
                    margin: 30px auto;
                }
                @include mob375 {
                    width: 100%;
                }
            }
            a {
                width: 140px;
                position: relative;
                margin-left: auto;
                @include Sm {
                    margin: auto;
                }
                &:before {
                    left: -25px;
                    top: -10px;
                }
            }
        }
    }
}

