                line-height: 1.8;
                color: $p;
                text-align: justify;
                margin-bottom: 50px;
            }
            a.link {
                width: 140px;
                margin-left: auto;
                background-color: $main-color;
                &::before {
                    left: -25px;
                    top: -10px;
                    border: 2px solid $main-color;
                }
            }
        }
    }
}

