                    padding-left: 10px;
                }
            }
            .click {
                @extend %flex;
                justify-content: space-between;
                width: 100%;
                cursor: pointer;
                display: none;
                @include Sm {
                    display: flex;
                }
                h2 {
                    color: $p;
                    font-size: 20px;
                    font-family: "Poppins-Regular";
                }
                span {
                    color: $p;
                    font-size: 14px;
                    display: block;
                    position: relative;
                    left: 0;
                    top: 0;
                    transition: all .5s ease-in-out;
                }
            }
            ul {
                @include Sm {
                    display: none;
                    &.open {
                        display: block;
                    }
                }
                li {
                    margin-bottom: 10px;
                    a {
                        color: $p;
                        font-size: 15px;
                        font-family: "Poppins-Regular";
                    }
                }
            }
        }
        .contact-us {
            display: block;
            width: 38%;
            margin-left: 2%;
            @include Sm {
                width: 100%;
                margin-top: 40px;
                text-align: center;
            }
            @include mob580 {
                width: 100%;
                margin-top: 0;
            }
            @include mob375 {
                margin-top: 40px;
            }
            h2 {
                color: $p;
                font-size: 15px;
                font-family: "Poppins-Regular";
                margin-bottom: 30px;
            }
            form {
                @extend %flex;
                @include mob375 {
                    display: block;
                }
                input {
                    height: 50px;
                    width: 70%;
                    padding-left: 20px;
                    border: 2px solid $border;
                    @include mob375 {
                        width: 100%;
                    }
                    &::placeholder {
                        font-size: 15px;
                        font-family: "Poppins-Regular";
                        color: $p;
                    }
                }
                button {
                    height: 50px;
                    width: 30%;
                    margin-left: 10px;
                    background-color: $main-color;
                    font-family: "Poppins-Regular";
                    font-size: 15px;
                    cursor: pointer;
                    @include mob375 {
                        width: 40%;
                        margin-top: 20px;
                    }
                }
            }
            // social
            .social {
                @extend %flex;
                @include Sm {
                    justify-content: center;
                }
                li {
                    margin: 30px 30px 0 0;
                    a {
                        display: block;
                        text-align: center;
                        i {
                            font-size: 20px;
                            color: $white-color;
                            width: 40px;
                            height: 40px;
                            line-height: 40px;
                            border-radius: 50%;
                            @extend %trans;
                            &:hover {
                                color: $main-color;
                            }
                        }
                    }
                }
            }
        }
    }
}